.App {
  text-align: center;
}

.div{
  box-sizing: border-box;
}

.list{padding-left : 20px;
text-align:left;
border-bottom: 1px solid grey;}

.modal{position: relative;
  display: inline-block;
  top: -900px;
  left: 200px;
  z-index: 0;
margin-top: 20px;
padding:20px;
background: rgb(170, 123, 123);
text-align: left;
transition: all 0.3s;
animation: modal  1s;
animation-iteration-count: 1;
}
@keyframes modal{
  0%{top:-900px;
  left:400px}
  100%{top: -900px;
    left: 200px;}  
}


.goodimo{font-size: 15px;
cursor: pointer;
transition: all 0.56s;}
.goodimo:hover{font-size: 30px;}
.title001 { background-color:  #61dafb;
display: inline-block;
padding: 20px;}
.title001:hover {background-color:  rgb(177, 255, 185);}

.black-nav {
  display: flex;
  background-color: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
